import { systemSetting } from './childRoutes/system.setting';
import { orders } from './childRoutes/orders';

const routes = [
  {
    path: '/login',
    name: 'login',
    component: lazy(() => import('@/pages/login/index')),
  },
  {
    path: '/',
    name: 'layout',
    component: lazy(() => import('@/pages/layout/index')),
    children: [
      {
        path: '/',
        name: 'home',
        component: lazy(() => import('@/pages/home/index')),
      },
      ...systemSetting,
      ...orders
      // ...manage,
      // ...diary,
      // ...moment,
    ],
  },
];

export default routes;
